.clock-comp{width:800px;margin:50px auto;display:flex;justify-content:space-around;font-size:62px;font-weight:800}.clock-comp .time-sect{width:130px;color:#fff;padding:20px 10px;margin:0 20px;font-size:80px;background-color:#000;border-radius:10px;box-sizing:border-box;display:flex;justify-content:center;align-items:center}.clock-comp .time-symbol{font-size:80px;line-height:80px;padding:20px 0px}.merchani-clock-container{margin:50px 0}.merchani-clock-container .clock-border-field{width:250px;height:250px;margin:0 auto;padding:5px;border-radius:50%;background-color:#000}.merchani-clock-container .clock-inner-field{width:100%;height:100%;position:relative;border-radius:100%;background-color:#fff}.merchani-clock-container .center-dot{position:absolute;left:50%;top:50%;width:6px;height:6px;border-radius:50%;transform:translate(-50%, -50%);background-color:#bd2929;z-index:99}.merchani-clock-container .hour-pointer{width:4px;height:80px;position:absolute;left:50%;top:45px;transition:transform .3s linear;transform:translate(-50%, 0) rotate(0deg);transform-origin:bottom center;background-color:#123491}.merchani-clock-container .minute-pointer{width:4px;height:90px;position:absolute;left:50%;top:35px;transition:transform .3s linear;transform:translate(-50%, 0) rotate(0deg);transform-origin:bottom center;background-color:#184e8d}.merchani-clock-container .second-pointer{width:2px;height:100px;position:absolute;left:50%;top:25px;transform:translate(-50%, 0) rotate(45deg);transform-origin:bottom center;background-color:#ac2525}.merchani-clock-container .clock-number{font-size:24px;line-height:24px;position:absolute;text-align:center}.merchani-clock-container .number-twelve{top:5px;left:50%;transform:translate(-50%, 0)}.merchani-clock-container .number-three{top:50%;right:5px;transform:translate(0, -50%)}.merchani-clock-container .number-six{bottom:5px;left:50%;transform:translate(-50%, 0)}.merchani-clock-container .number-nine{top:50%;left:5px;transform:translate(0, -50%)}
