.animation-field{width:95%;min-height:400px;display:flex;margin:0 auto;flex-flow:column;justify-content:center;align-items:center;background-color:lightgrey}.animation-boxes-wrapper{width:100%;height:300px;display:flex;flex-flow:row wrap;justify-content:space-around;align-items:center;position:relative}.box1{width:150px;height:150px;background-color:green}.box2{width:150px;height:150px;background-color:gold}.box3{width:150px;height:150px;background-color:#f97d35}.box4{position:relative;width:150px;height:150px;background-color:#0f5d75}.box5{width:150px;height:150px;background-color:#9d6eb3;border-radius:50%}.box6{width:150px;height:150px;background-color:#e75076;transition:all .5s ease-in-out}.box7{width:150px;height:150px;background-color:#02c99e;border-radius:25%}.box6:hover{width:300px;height:300px}.box1.anim{animation:animation1 2s linear infinite}.box2.anim{animation:animation2 1s ease-in-out infinite}.box3.anim{animation:animation3 .5s ease-in-out infinite}.box4.anim{animation:animation4 1s ease-in-out infinite}.box5.anim{animation:animation5 1s ease-in-out infinite}.box7.anim{transform:translate(-150px, -50px);transition:transform 5s linear}@keyframes animation1{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes animation2{0%{transform:translate(0px, -20px)}25%{transform:translate(20px, 0px)}50%{transform:translate(0px, 20px)}75%{transform:translate(-20px, 0px)}100%{transform:translate(0px, -20px)}}@keyframes animation3{0%{margin-top:0}33%{margin-top:50px}66%{margin-top:-50px}100%{margin-top:0px}}@keyframes animation4{0%{left:0px;top:0px}20%{left:20px;top:-20px}40%{left:20px;top:20px}60%{left:-20px;top:-20px}85%{left:-20px;top:20px}100%{left:0px;top:0px}}@keyframes animation5{0%{transform:scale(1)}33%{transform:scale(1.5)}66%{transform:scale(0.9)}100%{transform:scale(1)}}
